.footer {
  padding-top: 56px;
  background-color: $color-black;
  color: white;

  @include on-tablet {
    padding-top: 88px;
  }

  &__content {
    @include page-grid;
  }

  &__logo {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 5;
    }

    &--position {
      margin-bottom: 40px;
    }
  }

  &__menu {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 3;
      grid-row: 2;
    }

    @include on-mid-dekstop {
      grid-column: 1 / 2;
    }

    &--position {
      margin-bottom: 40px;
    }
  }

  &__ul {
    display: flex;
    gap: 16px;
    flex-direction: column;
    margin: 0;
    padding: 0;

    &-title {
      @include upper-case;
      color: $color-grey;
    }
  }

  &__link {
  
    svg path {
      fill: $color-accent-orange;
      transition: all 0.3s;
    }

    text-decoration: none;
    color: white;
    transition: color 0.3s;

    &:hover,
    &:focus {
      color : $color-accent-orange;

      svg path {
        fill: white;
      }
    }
  }
  &__contact-us {
    font-size: 48px;
    grid-column: 1 / -1;

    @include upper-case;
    @include heading-size;

    @include on-tablet {
      grid-row: 1;
      grid-column: 5 / -1;
    }

    &--position {
      margin-bottom: 40px;
    }
  }

  &__call-us {
    display: flex;
    flex-direction: column;
    gap: 16px;

    @include on-tablet {
      grid-row: 2;
      grid-column: 5 / -1;
    }

    &__title {
      @include upper-case;
      color: $color-grey;
    }

    &--position {
      margin-bottom: 40px;
    }
  }

  &__email-us {
    display: flex;
    flex-direction: column;
    gap: 16px;
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 5 / -1;
    }

    @include on-mid-dekstop {
      grid-row: 2;
      grid-column: -3 / -1;
    }

    &__title {
      @include upper-case;
      color: $color-grey;
    }

    &__mail {
      color: white;
      text-decoration: none;

      transition: color 0.3s;
      &:hover{
        color : $color-accent-orange;
      }
    }

    &--position {
      margin-bottom: 40px;
    }
  }

  &__icons {
    display: flex;
    gap: 32px;

    grid-column: 1 / -1;

    &--position {
      margin-bottom: 40px;
    }

    @include on-tablet {
      grid-column: 1 / 5;
    }
  }

  &__water-mark {
    color: $color-grey;

    @include on-tablet {
      grid-column: 5 / -1;
    }


  }
}
